.profile__heading {
    font-family: 'Inter', Arial, sans-serif;
    font-weight: 500;
    max-width: 294px;
    font-size: 42px;
    line-height: 48px;
    color: white;
    margin: 22px 0 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (max-width: 670px) {
    .profile__heading {
        align-self: center;
        font-size: 27px;
        line-height: 33px;
        text-align: center;
    }
}